import React, { Component } from 'react'
import Dice from '../Dice/Dice';
import './DiceContainer.css';

class DiceContainer extends Component {
    constructor(props) {
        super(props)
        this.state = {
            die1: 1,
            die2: 1,
            rolling: false
        }
        this.handleClick = this.handleClick.bind(this);
    }

    handleClick() {
        const num1 = Math.floor(Math.random() * 10) + 1;
        const num2 = Math.floor(Math.random() * 10) + 1;
        this.setState({ rolling: true })

        setTimeout(() => {
            this.setState({ die1: num1, die2: num2, rolling: false })
        }, 1000);
    }

    render() {
        return (
            <div className={`DiceRollContainer ${this.state.rolling ? 'container-wobble' : '' }`}>
                <div className="DiceContainer">
                    <Dice diceNum={this.state.die1} color={"black"} isRolling={this.state.rolling} />
                    <Dice diceNum={this.state.die2} color={"red"} isRolling={this.state.rolling} />
                </div>
                <div className="DiceContainer-roll-btn-container">
                    <button 
                        className="DiceContainer-roll-btn" 
                        onClick={this.handleClick}
                        disabled={this.state.rolling}>
                        {this.state.rolling ? 'Rolling...' : 'Roll'}
                    </button>
                </div>
            </div>
        );
    }
}

export default DiceContainer;