import React, { Component } from 'react'
import './Dice.css';

class Dice extends Component {
    render() {
        return (
            <div className={`Dice ${this.props.color} ${this.props.isRolling ? 'wobble' : ''}`}>
                {this.props.diceNum}
            </div>
        )
    }
}

export default Dice;